import React from "react";
import "../style/ProjectPage.css";

//    (,   )
const PROJECTS = [
  { name: "CIara", image: "/ciara.png" },
  { name: "WEHip", image: "/wehip.png" },
  { name: "", image: "/ypbook.png" },
  { name: " ", image: "/hillpark.png" },
  { name: "GENTLE MONSTER", image: "/gmshop.png" }
];

export default function ProjectPage({ onProjectClick }) {
  return (
    <div className="projectpage-wrap">
      <div className="project-title">MY PROJECTS</div>
      <div className="project-list">
        {PROJECTS.map((pj, idx) => (
          <div
            className="project-thumb"
            key={pj.name}
            onClick={() => onProjectClick(idx)}
          >
            <img src={process.env.PUBLIC_URL + pj.image} alt={pj.name} className="project-thumb-img" />
            <div className="project-thumb-name">{pj.name}</div>
          </div>
        ))}
      </div>
    </div>
  );
}