import { useEffect, useRef, useState } from "react";
import { Howl } from "howler";
import gsap from "gsap";
import "../style/PressStart.css";

const pressSound = new Howl({
  src: [require("../sound/press-g-sound.mp3")],
  volume: 0.7,
});

const text = "PRESS START";

export default function PressStart({ onStart }) {
  const [clicked, setClicked] = useState(false);
  const [hasAnimated, setHasAnimated] = useState(false);
  const letterRefs = useRef([]);

  useEffect(() => {
    letterRefs.current.forEach((el) => {
      if (!el) return;
      gsap.set(el, {
        y: 400,
        opacity: 0,
      });
    });
    gsap.to(letterRefs.current, {
      y: 0, opacity: 1,
      duration: 0.54,
      stagger: 0.07,
      ease: "back.out(1.7)",
      onComplete: () => setHasAnimated(true),   // slideUp  hasAnimated true
    });
  }, []);

  const handleClick = () => {
    if (clicked) return;
    setClicked(true);
    pressSound.play();
    setTimeout(() => {
      onStart();
    }, 1000);
  };

  return (
    <div className="press-start-container">
      <img
        src={process.env.PUBLIC_URL +"/main-gamelogo-01.png"}
        alt="HEEWON'S PORTFOLIO"
        className="press-logo-img"
        draggable={false}
      />
      <h1
        className={
          "press-start-text" +
          (clicked ? " press-start-clicked" : "") +
          (hasAnimated ? " animated" : "")
        }
        onClick={handleClick}
      >
        {text.split("").map((ch, idx) =>
          <span
            key={idx}
            ref={el => letterRefs.current[idx] = el}
            className={ch === " " ? "press-space" : "press-letter"}
          >
            {ch}
          </span>
        )}
      </h1>
    </div>
  );
}