import React, { useState } from "react";
import MainBg from "./components/MainBg";
import PressStart from "./components/PressStart";
import LoadingScreen from "./components/LoadingScreen";
import SelectUser from "./components/SelectUser";
import LinkStart from "./components/LinkStart";
import MainPage from "./components/MainPage";
import ProjectPage from "./components/ProjectPage";
import Contact from "./components/Contact";
import Project1 from "./components/Project1";
import Project2 from "./components/Project2";
import Project3 from "./components/Project3";
import Project4 from "./components/Project4";
import Project5 from "./components/Project5";
import "./App.css";
import CircleNav from "./components/CircleNav";

export default function App() {
  // stage "intro", "main"
  const [stage, setStage] = useState("intro");
  //   
  const [introStep, setIntroStep] = useState(0); // 0~3
  //  
  const [selectedProject, setSelectedProject] = useState(null);

  const detailComponents = [
    <Project1 onClose={() => setSelectedProject(null)} />,
    <Project2 onClose={() => setSelectedProject(null)} />,
    <Project3 onClose={() => setSelectedProject(null)} />,
    <Project4 onClose={() => setSelectedProject(null)} />,
    <Project5 onClose={() => setSelectedProject(null)} />
  ];

  //   (4)
  const renderIntro = () => {
    switch (introStep) {
      case 0:
        return <PressStart onStart={() => setIntroStep(1)} />;
      case 1:
        return <LoadingScreen onLoaded={() => setIntroStep(2)} />;
      case 2:
        return <SelectUser onSelect={() => setIntroStep(3)} />;
      case 3:
        return <LinkStart onStart={() => setStage("main")} />;
      default:
        return null;
    }
  };

   function goPage(pageNum) {
    if (pageNum === 1) {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    } else if (pageNum === 2) {
      document.querySelector('.project-section')?.scrollIntoView({ behavior: 'smooth' });
    } else if (pageNum === 3) {
      document.querySelector('.contact-section')?.scrollIntoView({ behavior: 'smooth' })
    }
  }


  return (
    <>
      <MainBg />
      {stage === "intro" && renderIntro()}
      {stage === "main" && (
        <div className="content-wrap">
          <div className="main-section">
            <MainPage />
          </div>
          <div className="project-section">
            {selectedProject === null
              ? <ProjectPage onProjectClick={setSelectedProject} />
              : detailComponents[selectedProject]
            }
          </div>
          <div className="contact-section">
            <Contact />
          </div>
          <CircleNav goPage={goPage}/>
        </div>
      )}
    </>
  );
}