import React, { useState } from "react";
import "../style/MainPage.css";

const skillList = [
  { name: "HTML5", img: "/html.png", percent: 90 },
  { name: "CSS3", img: "/css.png", percent: 80 },
  { name: "JavaScript", img: "/javascript.png", percent: 80 },
  { name: "React", img: "/react.png", percent: 75 },
  { name: "JQuery", img: "/jquery.png", percent: 65 },
  { name: "Redux", img: "/redux.png", percent: 70 },
  { name: "SCSS", img: "/sass.png", percent: 75 },
  { name: "Figma", img: "/figma.png", percent: 80 },
];

function CircleProgress({ percent }) {
  const radius = 54;
  const stroke = 7;
  const norm = radius - stroke / 3;
  const circum = 2 * Math.PI * norm;
  const offset = circum - (percent / 100) * circum;
  return (
    <svg width={120} height={120} className="circle-progress">
      <circle
        cx={60}
        cy={60}
        r={norm}
        fill="transparent"
        stroke="#eee"
        strokeWidth={stroke}
      />
      <circle
        cx={60}
        cy={60}
        r={norm}
        fill="none"
        stroke="#7d39bb"
        strokeWidth={stroke}
        strokeDasharray={circum}
        strokeDashoffset={offset}
        strokeLinecap="round"
        style={{ transition: "stroke-dashoffset 0.6s" }}
      />
      <text
        x="60"
        y="70"
        textAnchor="middle"
        fontSize="27"
        fill="#222"
        fontFamily="DungGeunMo, monospace"
        fontWeight="bold"
      >
        {percent}%
      </text>
    </svg>
  );
}

export default function MainPage() {
  const [tab, setTab] = useState("profile");
  const [activeSkill, setActiveSkill] = useState(null);

  const handleSkillClick = (i) => {
    if (activeSkill === i) setActiveSkill(null); //   
    else setActiveSkill(i);
  };

  return (
    <div className="main-bg">
      <div className="main-content">
        {/*  */}
        <div className="tab-wrap">
          <button className={tab === "profile" ? "active" : ""} onClick={() => setTab("profile")}>profile</button>
          <button className={tab === "skill" ? "active" : ""} onClick={() => setTab("skill")}>My Skill</button>
        </div>
        {tab === "profile" ? (
          <div className="profile-card">
            <div className="profile-img-col">
              <img src={process.env.PUBLIC_URL +"/pro.png"} alt="" className="profile-img" />
              <div className="profile-left-detail"><br /><br />
                <div className="left-row"><span>&nbsp;|&nbsp;</span> </div><br /><br />
                <div className="left-row"><span>&nbsp;|&nbsp;</span>1</div><br /><br />
                <div className="left-row"><span>&nbsp;|&nbsp;</span>(, )   </div>
                <div className="profile-info">
                  <h2></h2>
                  <div className="profile-sub">2002.11.30</div>
                  <div className="profile-email">
                    <b>E :</b> yhw1130y@gmail.com
                  </div>
                  <div className="profile-addr">
                    <b>A :</b>     201
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
         <div className="skill-card">
  <div className="skill-row">
    {skillList.map((skill, i) => (
      <div
        key={skill.name}
        className={`skill-box${activeSkill === i ? " active" : ""}`}
        onClick={() => handleSkillClick(i)}
      >
        {activeSkill === i ? (
          <div className="skill-progress-bg">
            <CircleProgress percent={skill.percent} />
          </div>
        ) : (
          <div className="skill-img-bg">
            <img src={process.env.PUBLIC_URL + skill.img} alt={skill.name} />
          </div>
        )}
      </div>
    ))}
  </div>
</div>
        )}
      </div>
    </div>
  );
}