import { useState, useEffect } from "react";
import "../style/SelectUser.css";
import PixelCatProfile from "../components/PixelCatProfile"; // !

const users = [
  {
    icon: "?", // ()
    name: "? ? ?",
    lv: "? ? ?",
    class: "? ? ?",
    since: "? ? ?"
  },
  {
    icon: "profile", // :  
    name: "Y H W",
    lv: "2 4",
    class: "W D",
    since: "2002"
  },
  {
    icon: "?",
    name: "? ? ?",
    lv: "? ? ?",
    class: "? ? ?",
    since: "? ? ?"
  }
];

export default function SelectUser({ onSelect }) {
  const [selected, setSelected] = useState(1);

  useEffect(() => {
    const onKey = (e) => {
      if (e.key === "ArrowLeft") setSelected((i) => Math.max(i - 1, 0));
      if (e.key === "ArrowRight") setSelected((i) => Math.min(i + 1, 2));
      if (e.key === "Enter") onSelect?.(selected);
    };
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, [selected, onSelect]);

  return (
    <div className="selectuser-bg">
      {/*    */}
      <h2 className="selectuser-title">select character</h2>
      <div className="selectuser-row">
        {users.map((u, idx) => (
          <div
            className={`selectuser-slot${selected === idx ? " selected" : ""}`}
            key={idx}
            onClick={() => setSelected(idx)}
            tabIndex={0}
          >
            {selected === idx && (
              <div className="selectuser-arrow"></div>
            )}
            <div className="selectuser-icon">
              {u.icon === "profile" ? (
                <PixelCatProfile size={60} />
              ) : (
                <span className="pixel-q">?</span>
              )}
            </div>
            <div className="selectuser-info">
              <div>name : {u.name}</div>
              <div>Lv : {u.lv}</div>
              <div>class : {u.class}</div>
              <div>since : {u.since}</div>
            </div>
            <button
              className={`selectuser-start${selected === idx ? " focused" : ""}`}
              onClick={() => onSelect?.(idx)}
              tabIndex={-1}
            >
              START
            </button>
          </div>
        ))}
      </div>
    </div>
  );
}
