export default function PixelCatProfile({ size = 64 }) {
  return (
    <svg
      viewBox="0 0 32 32"
      width={size}
      height={size}
      style={{ imageRendering: "pixelated", background: "none", display: "block" }}
      xmlns="http://www.w3.org/2000/svg"
    >
      {/*  */}
      <rect x="7" y="2" width="3" height="7" fill="#bb9cf4" />
      <rect x="22" y="2" width="3" height="7" fill="#bb9cf4" />

      {/* (, ) */}
      <rect x="9" y="8" width="14" height="11" fill="#bb9cf4" />
      <rect x="7" y="11" width="18" height="6" fill="#bb9cf4" />
      <rect x="8" y="19" width="16" height="3" fill="#bb9cf4" />

      {/* /  */}
      <rect x="5" y="21" width="22" height="3" fill="#bb9cf4" />
      <rect x="3" y="23" width="26" height="2" fill="#bb9cf4" />

      {/* , ,  */}
      <rect x="13" y="13" width="2" height="2" fill="#222" />
      <rect x="17" y="13" width="2" height="2" fill="#222" />
      <rect x="15" y="16" width="2" height="2" fill="#222" />
      <rect x="12" y="18" width="8" height="2" fill="#222" />
    </svg>
  );
}