import { useState } from "react";
import "../style/LinkStart.css";

export default function LinkStart({ onStart }) {
  const [pop] = useState(true);  // !   
  const [zoom, setZoom] = useState(false);
  const [clicked, setClicked] = useState(false);

 const handleClick = () => {
   if (zoom) return;
  setZoom(true);
   setClicked(true);
    setTimeout(() => {
      if (onStart) onStart();
   }, 580);
  };

  return (
    <div className="linkstart-bg">
      <span
        className={`linkstart-text${pop ? " pop" : ""}${zoom ? " zoom" : ""}`}
        tabIndex={0}
        onClick={handleClick}
        onKeyDown={e => (e.key === "Enter" || e.key === " " ? handleClick() : undefined)}
        style={{ cursor: zoom ? "default" : "pointer", userSelect: "none" }}
        aria-label="LINK START!"
      >
        LINK START!
      </span>
    </div>
  );
}