import React, { useEffect, useRef } from "react";
import '../style/Project5.css'

function Project5({ onClose }) {
  const imgScrollRef = useRef(null);

  //    
  useEffect(() => {
    const el = imgScrollRef.current;
    if (!el) return;
    let pos = 0;
    let dir = 1;
    let raf;
    const speed = 0.7;
    const step = () => {
      const max = el.scrollHeight - el.clientHeight;
      pos += dir * speed;
      if (pos >= max) {
        pos = max;
        dir = -1;
      } else if (pos <= 0) {
        pos = 0;
        dir = 1;
      }
      el.scrollTop = pos;
      raf = requestAnimationFrame(step);
    };
    raf = requestAnimationFrame(step);
    return () => cancelAnimationFrame(raf);
  }, []);

  return (
    <section className="pj1-section game-shadow-bg">
      <button className="pj1-back-btn" onClick={onClose}>
        BACK !
      </button>
      <div className="pj1-inner game-border">
        <div className="pj1-left">
          <div className="pj1-img-frame pj1-tone-down game-glow">
            <div className="pj1-img-scroll" ref={imgScrollRef}>
              {/*      */}
              <img
                src={process.env.PUBLIC_URL + "/gmshopfull.png"}
                alt="2  "
                className="pj1-img"
              />
            </div>
          </div>
        </div>
        <div className="pj1-right">
          <div className="pj1-label">React project</div>
          <div className="pj1-title">GENTLE MONSTER</div>
          <span className="pj1-grad">  </span>
          <div className="pj1-desc">
            React      . <br />
                     . <br />
            React      ,  .
          </div>
          <div className="pj1-tags">
            <span>#HTML5</span>
            <span>#CSS3</span>
            <span>#React</span>
          </div>
          <div className="pj1-btns">
            <a
              href="https://yhw1130y.github.io/uiuxproject001/"
              target="_blank"
              rel="noopener noreferrer"
              className="pj1-btn game-btn"
            >
              Demo
            </a>
            <a
              href="https://yhw1130y.github.io/uiuxproject001/ciara.pdf"
              target="_blank"
              rel="noopener noreferrer"
              className="pj1-btn game-btn"
            >
              Report
            </a>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Project5;
