import { useEffect, useRef } from "react";
import { Howl } from "howler";
import Typed from "typed.js";
import "../style/LoadingScreen.css";

const loadSound = new Howl({
  src: [require("../sound/load.mp3")],
  volume: 0.7,
});

export default function LoadingScreen({ onLoaded }) {
  const typedRef = useRef(null);
  const barRef = useRef(null);

  useEffect(() => {
    const totalDuration = 2500; // 3
    const soundDelay = 500;    // 2   

    //  ( )
    const typed = new Typed(typedRef.current, {
      strings: ["loading..."],
      typeSpeed: 60,
      backSpeed: 0,
      loop: false,
      showCursor: false,
    });

    
    let start = 0;
    const interval = setInterval(() => {
      start += 20;
      if (barRef.current) {
        barRef.current.style.width = `${(start / totalDuration) * 100}%`;
      }
      if (start >= totalDuration) {
        clearInterval(interval);
      }
    }, 20);

    // 2   
    const soundTimer = setTimeout(() => {
      loadSound.play();
    }, soundDelay);

    // 4.5   
    const endTimer = setTimeout(() => {
      typed.destroy();
      onLoaded();
    }, totalDuration);

    return () => {
      clearTimeout(soundTimer);
      clearTimeout(endTimer);
      clearInterval(interval);
      typed.destroy();
      loadSound.stop();
    };
  }, [onLoaded]);

  return (
    <div className="loading-bg">
      <div className="loading-window-stack">
        <div className="loading-window layer-3"></div>
        <div className="loading-window layer-2"></div>
        <div className="loading-window layer-1">
          <div className="loading-contents">
            <div className="loading-title">LOADING.EXE</div>
            <div className="loading-content">
              <span ref={typedRef}></span>
            </div>
            <div className="loading-bar-bg">
              <div ref={barRef} className="loading-bar" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}