import React from "react";
import "../style/CircleNav.css";

const PAGES = [
  { num: 1, label: "Main" },
  { num: 2, label: "Project" },
  { num: 3, label: "Contact" },
];

export default function CircleNav({ currentPage, goPage }) {
  return (
    <div className="circle-nav-vertical">
      {PAGES.map((page) => (
        <button
          key={page.num}
          className={
            "circle-nav-btn" + (currentPage === page.num ? " active" : "")
          }
          onClick={() => goPage(page.num)}
        >
          <span className="circle-dot">{page.num}</span>
        </button>
      ))}
    </div>
  );
}